<template>
    <div class="SearchC">
        <div class="row">
            <div class="text">GOODS</div>
            <el-input v-model="search" placeholder="请输入内容" clearable :disabled="isdisable"></el-input>
            <div class="switch">
                <el-switch v-model="valuetext" active-color="#13ce66"  inactive-color="#ff4949" @change="changeSwitch"></el-switch>
            </div>
        </div>
        <div>
            <el-table :data="tables" style="width: 100%">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item label="所属店铺">
                                <span>{{ props.row.shop }}</span>
                            </el-form-item>
                            <el-form-item label="店铺 number">
                                <span>{{ props.row.shopId }}</span>
                            </el-form-item>
                            <el-form-item label="商品分类">
                                <span>{{ props.row.category }}</span>
                            </el-form-item>
                            <el-form-item label="店铺地址">
                                <span>{{ props.row.address }}</span>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column label="item ID" prop="id"></el-table-column>
                <el-table-column label="product name" prop="name"></el-table-column>
                <el-table-column label="price" prop="desc"></el-table-column>
            </el-table>
        </div>
        <el-backtop  :bottom="60" :visibility-height="1600">
            <div
            style="{
                height: 100%;
                width: 100%;
                background-color: #f2f5f6;
                box-shadow: 0 0 6px rgba(0,0,0, .12);
                text-align: center;
                line-height: 40px;
                color: #32CD32;
                border-radius: 100%;
            }"
            >
            UP
            </div>
        </el-backtop>
        
    </div>
</template>

<script>
export default {
    name:'SearchC',
     data() {
        return {
            search: '',
            valuetext: true,
            isdisable:false,
            tableData: [{
                id: '1',
                name: 'ipad Pro',
                category: 'ipad',
                desc: '6799-17299',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '010111'
            }, {
                id: '2',
                name: 'ipad Air',
                category: 'ipad',
                desc: '4799-5999',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '020222'
            }, {
                id: '3',
                name: 'ipad 第十代',
                category: 'ipad',
                desc: '3599-5999',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '030333'
            }, {
                id: '4',
                name: 'ipad 第九代',
                category: 'ipad',
                desc: '2599-3799',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '040444'
            },{
                id: '5',
                name: 'ipad mini',
                category: 'ipad',
                desc: '3999-5199',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '0404445'
            },{
                id: '6',
                name: 'iphone 14 Pro',
                category: 'iphone',
                desc: '7999-12499',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '0404446'
            },{
                id: '7',
                name: 'iphone 14',
                category: 'iphone',
                desc: '5999-8699',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '0404447'
            },{
                id: '8',
                name: 'iphone SE',
                category: 'iphone',
                desc: '3499-4699',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '0404448'
            },{
                id: '9',
                name: 'iphone 13',
                category: 'iphone',
                desc: '5399-8099',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '0404449'
            },{
                id: '10',
                name: 'iphone 12',
                category: 'iphone',
                desc: '4699-6099',
                address: 'Apple',
                shop: 'Apple Store',
                shopId: '04044410'
            }]
        }
    },
    methods: {
        changeSwitch(){
            if(this.valuetext!=true){
                this.isdisable=true
            }
            else{
                this.isdisable=false
            }
        }

    },
    computed: {
        tables(){
            let search = this.search.toLowerCase();
            if(search){
            // tableData是要放在表格中展示的数据
                return this.tableData.filter(data =>{
                    // 获取对象数组的所有枚举属性，通过some来判断对象中的某一属性是否匹配上，如果匹配上了那么这个对象后面的属性就不用判断了，从而返回整个对象(毕竟我们要看整个对象的值)。
                    return Object.keys(data).some(key =>{
                        if(data[key] !== null) // 对象的属性不为空才进行模糊匹配，否则会报错
                            return data[key].toString().toLowerCase().indexOf(search) > -1;
                    });
                });
            }else{
                return this.tableData; // 如果搜索框没有值，就正常展示表格的所有数据
            }
        },
        

    },

}
</script>



<style scoped>
    .SearchC{
        height: 800px;
        /* background-color: #e28f13; */
    }
    
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .row{
        display: flex;
        flex-direction: row;
    }
    .text{
        width: 100px;
        text-align: center;
        line-height: 40px;
    }
    .switch{
        width: 100px;
        text-align: center;
        line-height: 40px;
    }
    

</style>