<template>
    <div class="NavigationC">
        <i class="el-icon-edit" ></i>
        <div class="many">
            <el-breadcrumb separator="|">
            <el-breadcrumb-item :to="{ path: '/' }">Pro</el-breadcrumb-item>
            <el-breadcrumb-item><a href="https://www.npmjs.com/products/teams">Teams</a></el-breadcrumb-item>
            <el-breadcrumb-item class="text" @click.native="openlogin">Login</el-breadcrumb-item>
            <el-breadcrumb-item class="text">Documentation</el-breadcrumb-item>
        </el-breadcrumb>
        </div>
        <p @click="changetext"  :style="active" @mouseover="yr" @mouseleave="yc">{{text}}</p>
    </div>
    
</template>

<script>
export default {
    name:'NavigationC',
     data() {
        return {
           active:'',
           text:'Noisy Pneumatic Mnemoni',
           temp:0
        }
    },
    methods: {
        changetext(){
            if(this.temp==0){
                this.active="color:gray",
                this.text="Neverending Programming Mistakes"
                this.temp=1

            }
            else{
                this.active ='',
                this.text="Noisy Pneumatic Mnemoni"
                this.temp=0
            }
            
        },
        yr(){
            this.active="color:gray"
        },
        yc(){
            this.active =''
        },
        openlogin(){
            const h = this.$createElement;
            this.$message({
            message: h('p', null, [
                h('span', null, 'welcome to'),
                h('i', { style: 'color: teal' }, '  0ero1ne')
            ])
            });
            this.$store.commit("Openloginstore");
        },
        
    },
}
</script>



<style scoped>
    .NavigationC{
    
        width: 100%;
        height: 50px;
        background-color:rgb(56,100, 139);
    }
    p{
        color: black;
        width: 400px;
        height: 20px;
        margin-top: -20px;
        margin-left: 100px;
    }
    .many{
        float: right;
        margin-top: 20px;
        margin-right: 60px;
    }
    i{
        margin-top: 20px;
        margin-left: 40px;
    }
    .text >>> .el-breadcrumb__inner{
        color: #778899;
        
    }


</style>