<template>
    <div class="ModelC">
        <div >
            <el-image :src="src" class="block"></el-image>
        </div>
        <div class="picture">
            <img src="https://i.postimg.cc/YSGZvdMQ/23.jpg"  class="labi" >
        </div>
        <div class="span1" >
            <h1>0ero 1ne.</h1>
        </div>
        <div class="button" v-show="hidewelcome"> 
            <el-button :plain="true" @click="openVn" class="button1" >welcome</el-button> 
        </div>
        <transition name="el-fade-in-linear">
            <div class="login" v-show="$store.state.showcontent">
                <div class="loginbox">
                    <i class="el-icon-toilet-paper" @click="shutloginbox"></i>
                    <div class="logininput">
                        <el-input
                            placeholder="请输入内容"
                            prefix-icon="el-icon-postcard"
                            v-model="inputusername"
                            clearable
                            style="width:200px">
                        </el-input>
                    </div>
                    <div class="logininput">
                        <el-input 
                            placeholder="请输入密码" 
                            prefix-icon="el-icon-key"
                            v-model="inputpassword" 
                            show-password 
                            style="width:200px ">
                        </el-input>
                    </div>
                    <div class="logininput">
                        <el-button round style="box-shadow:  0px 5px 5px rgba(240,248,255, 0.6);" @click="logining">
                            Login
                        </el-button>   
                    </div>
                </div> 
                
            </div>
        </transition>
        <div class="btnContainer">
            <div class="btn no-highlight" id="btn" @click="onBtnUp">
                <div class="knob no-highlight">
                    <div class="light no-highlight"></div>
                    <div class="top no-highlight"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script >
import gsap from "gsap"

export default {
    name:'ModelC',
     data() {
        return {
            inputusername: '',
            inputpassword: '',
            hidewelcome:true,
            src: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
            isChecked:false,
        }
    },
    methods: {
        openVn() {
            const h = this.$createElement;
            this.$message({
            message: h('p', null, [
                h('span', null, 'welcome to'),
                h('i', { style: 'color: teal' }, '  0ero1ne')
            ])
            });
            this.$store.commit("Openloginstore");
            this.hidewelcome=!this.hidewelcome;
        },
        logining(){
            const h = this.$createElement;
            this.$message({
            message: h('p', null, [
                h('span', null, 'logging in  '),
                h('i', { style: 'color: teal' },this.inputusername)
            ])
            });
            this.$router.push({ path: '/ContentC' });
            this.$store.commit("Loginingstore"); 
        },
        shutloginbox(){
            this.hidewelcome=!this.hidewelcome;
            this.$store.commit("Shutloginstore");
        },
        onBtnUp(){
            
            const tl = gsap.timeline();
            this. isChecked = ! this.isChecked;

            let y = 0;
            let x=0;
            let backgroundColor = "#F0F8FF";
            let size = "80px";
            let opacity=0.9
            

            if (this.isChecked) {
                y = 80;
                x = 300;
                backgroundColor = "#FFFFFF";
                size = "500px";
                opacity=1
            }

            tl.to(".knob", { x,y, duration: 1 }, "up");
            tl.to(".top", { backgroundColor, duration: 2 }, "up");
            tl.to(".light", { width: size, height: size, duration: 2 }, "up");
            tl.to(".block", {opacity, duration: 1 }, "up");
            
        }

    }
}
</script>



<style scoped>
    
    .block{
        filter: brightness(1);
    }
    .picture{
        width: 240px;
        height: 300px;
        position:absolute;
        top: 320px;
        text-align: center;
        z-index: 1;
    }
    .labi{
        width: 100px;
        height: 100px;
        border-radius: 100%;  
    }
    .span1{
        width: 240px;
        position: absolute;
        top: 450px;
        text-align: center;
        animation: typing 2s steps(22), blink .5s step-end infinite alternate;
        white-space: nowrap;
        overflow: hidden;
   }
   @keyframes typing {
    from {
        width: 0px
    }
    }
    
    @keyframes blink {
    50% {
        border-color: transparent
    }
    }
   h1{
        color: rgb(108, 168, 142);
   }
   .button{
        position: fixed;
        bottom: 40px;
        width: 240px;
        text-align: center;
        z-index: 1;
   }
   .button1{
        height: 60px;
        width: 160px;
        border-radius: 30%;
        box-shadow:  0px 10px 10px rgba(240,248,255, 0.6);
   }
   
   .login{
        width: 100%;
        height: 220px;     
        position:absolute;
        top: 300px;
        text-align: center;
        
   }
   .loginbox{
        width: 240px; 
        height: 220px;     
        display: flex;
        flex-direction: column;
        margin: 0% auto;
        border-radius: 50px;
        border-radius: 50px;
        border-radius: 71px;
        background: linear-gradient(225deg, #325a7d, #3c6b95);
        box-shadow:  -26px 26px 100px #213b52,
                    26px -26px 100px #4f8dc4;
   }
   .logininput{
        width: 100%;
        text-align: center;
        margin-top: 20px;
   }
   /* el-input的圆角命令 */
    ::v-deep .el-input__inner {
        border-radius: 23px;
        height: 45px;
        background-color: transparent !important;
        color: #E6E6FA;
        border: 1px solid #E6E6FA;
        
    }
    /* 大月亮 */
    .btnContainer {
        width: 120px;
        height: 120px;
        position: absolute;
        top: 260px;
        left: 1%;
        text-align: center;
        
        
    }

    .btn {
        position: absolute;
        border-radius: 100%;
        padding: 10px;
        cursor: pointer;
        
    }

    .knob {
        width: 100px;
        height: 100px;
        position: relative;
    }

    .top {
        background-color: #F0F8FF;
        border-radius: 100%;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }

    .light {
        border-radius: 100%;
        position: absolute;
        width: 100px;
        height: 100px;
        background: radial-gradient(
            50% 50% at 50% 50%,
            #4cc3e2 10.42%,
            rgba(94, 199, 227, 0.791579) 27.08%,
            rgba(113, 204, 229, 0.35) 45.31%,
            rgba(144, 213, 231, 0.11) 65.1%,
            rgba(158, 216, 231, 0.02) 78.12%,
            rgba(177, 221, 233, 0) 95.83%
        );
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        
    }


 

</style>